@keyframes bounce {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-16px);
    }
    100% {
        transform: translateY(0);
    }
}
@keyframes grow-2 {
    0% {
        transform: scale(1) rotateZ(0deg) translateX(0) translateY(0);
    }
    100% {
        transform: scale(0.25) rotateZ(5deg) translateX(-8%) translateY(20%);
    }
}
@keyframes grow-3 {
    0% {
        transform: scale(1) rotateZ(0deg) translateX(0) translateY(0);
    }
    100% {
        transform: scale(1.5) rotateZ(12deg) translateX(20%) translateY(5%);
    }
}
@keyframes grow-4 {
    0% {
        transform: scale(1) rotateZ(0deg) translateX(0) translateY(0);
    }
    100% {
        transform: scale(0.75) rotateZ(45deg) translateX(10%) translateY(-2%);
    }
}
@keyframes grow-5 {
    0% {
        transform: scale(1) rotateZ(0deg) translateX(0) translateY(0);
    }
    100% {
        transform: scale(0.1) rotateZ(-30deg) translateX(-40%) translateY(8%);
    }
}
body {
    color: $text-color;
    &.fonts-loaded {
        font-family: $font-stack;
        font-weight: 400;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            font-weight: 400;
        }
    }
}
a,
a:visited {
    @include animated;
    text-decoration: none;
    color: inherit;
    &:hover {
        opacity: 0.75;
    }
}
strong {
    font-weight: 700;
}
.button {
    display: inline-block;
    margin-left: 10px;
    max-width: initial;
    padding: 12px 16px 8px;
    margin-top: 0;
    background-color: transparent;
    font-weight: 400;
    text-transform: none;
    text-decoration: none;
    color: white;
    border: 1px solid transparent;
    font-size: 18px;
    font-family: $font-stack;
    @media only screen and (max-width: $phone-breakpoint) {
        padding: 8px 12px 4px;
        font-size: 16px;
    }
    &:first-child {
        margin-left: 0;
    }
    &.primary {
        background-color: $color-primary;
        &.inverse {
            background-color: transparent;
            border-color: $color-primary;
            color: $color-primary;
        }
    }
    &.secondary {
        background-color: $color-secondary;
        &.inverse {
            background-color: transparent;
            border-color: $color-secondary;
            color: $color-secondary;
        }
    }
    &.negative {
        background-color: $color-negative;
        &.inverse {
            background-color: transparent;
            border-color: $color-negative;
            color: $color-negative;
        }
    }
}
header {
    position: fixed;
    top: 0;
    z-index: 9999;
    width: 100%;
    height: 110px;
    background-color: $header-background;
    pointer-events: none;
    #kb-logo {
        position: absolute;
        top: 30px;
        left: 30px;
        z-index: 9997;
        padding: 10px;
        width: 50px;
        height: 50px;
        border: 1px solid #fff;
        border-radius: 50px;
        pointer-events: all;
    }
    #kb-logo img {
        width: 28px;
        height: auto;
        padding-top: 4px;
        padding-left: 1px;
    }
    #kb-logo:focus {
        outline: none;
    }
}
#main {
    background-color: $main-background;
    position: relative;
    z-index: 1;
    height: 100vh;
    overflow: hidden;
    .background {
        font-family: $font-stack;
        color: white;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        font-size: 40vw;
        line-height: 36vw;
        word-break: normal;
        filter: opacity(8%);
        text-align: right;
        letter-spacing: -1vw;
        overflow: hidden;
        ::selection {
            background: transparent;
        }
        ::-moz-selection {
            background: transparent;
        }
        > span {
            display: inline-block;
            pointer-events: none;
            &:nth-child(2n + 1) {
                animation: grow-2 75000ms infinite alternate;
            }
            &:nth-child(3n + 1) {
                animation: grow-3 75000ms infinite alternate;
            }
            &:nth-child(4n + 1) {
                animation: grow-4 75000ms infinite alternate;
            }
            &:nth-child(5n + 1) {
                animation: grow-5 75000ms infinite alternate;
            }
        }
    }
    #user-text {
        font-family: $font-stack;
        border: none;
        background: transparent;
        display: block;
        font-size: 50px;
        line-height: 64px;
        font-style: normal;
        border-bottom: 1px solid $text-color;
        padding: 20px 0;
        margin-bottom: 20px;
        width: 100%;
        height: 233px; // Default
        resize: none;
        overflow: hidden;
        color: $text-color;
        -ms-overflow-style: none;
        @media only screen and (max-width: $tablet-portrait-breakpoint) {
            font-size: 42px;
            line-height: 56px;
        }
        @media only screen and (max-width: $phone-breakpoint) {
            font-size: 26px;
            line-height: 40px;
        }
        &:focus {
            outline: none;
        }
        &.regular {
            font-weight: 400;
        }
        &.bold {
            font-weight: 700;
        }
        &.light {
            font-weight: 300;
        }
        /*&.italic {
            font-style: italic;
        }*/
    }
    .subtitle {
        font-size: 18px;
        line-height: 30px;
    }
}
.select-container {
    position: relative;
    display: inline-block;
    margin-left: 4px;
    &:after {
        content: '';
        display: block;
        position: absolute;
        top: 10px;
        right: 8px;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 6px 4px 0 4px;
        border-color: #00ad8c transparent transparent transparent;
    }
    select {
        display: inline-block;
        font-size: 13px;
        font-family: inherit;
        vertical-align: top;
        padding: 6px 24px 4px 6px;
        border-radius: 2px;
        border: none;
        color: #00ad8c;
        appearance: none;
        cursor: pointer;
        background-color: white;
        box-shadow: none;
    }
}
.button-container {
    position: absolute;
    bottom: 30px;
    right: 0;
    text-align: right;
    white-space: nowrap;
    @media only screen and (max-width: $tablet-landscape-breakpoint) {
        position: relative;
        bottom: initial;
        right: initial;
        margin-top: 16px;
        text-align: left;
    }
    .fa {
        @media only screen and (max-width: $phone-breakpoint) {
            display: none;
        }
    }
}
.scroll-chevron {
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    @media only screen and (max-width: $phone-breakpoint) {
        bottom: 10px;
    }
    .fa {
        animation: bounce 2000ms infinite;
    }
}
#about {
    background: $about-background;
    color: white;
    padding: 200px 0;
    position: relative;
    z-index: 9996;
    font-weight: 300;
    @media only screen and (max-width: $tablet-portrait-breakpoint) {
        padding: 160px 0;
    }
    @media only screen and (max-width: $phone-breakpoint) {
        padding: 80px 0;
    }
    a,
    a:visited {
        &:not(.button) {
            color: $color-secondary;
            text-decoration: underline;
        }
    }
    h2 {
        font-size: 42px;
        line-height: 50px;
        color: $color-primary;
        font-weight: 700;
    }
    .character-set {
        font-size: 38px;
        letter-spacing: 3px;
        line-height: 42px;
        text-align: right;
        font-family: $font-stack;
        font-weight: 400;
        @media only screen and (max-width: $tablet-landscape-breakpoint) {
            font-size: 32px;
            line-height: 36px;
            text-align: left;
            margin-bottom: 42px;
        }
        @media only screen and (max-width: $phone-breakpoint) {
            font-size: 28px;
            line-height: 32px;
        }
        p {
            margin-top: 16px;
            word-break: break-all;
            &:first-child {
                margin-top: 0;
            }
        }
        br {
            @media only screen and (max-width: $tablet-portrait-breakpoint) {
                display: none;
            }
        }
    }
    .stat {
        padding: 10px;
        text-align: center;
        border: 1px solid white;
        .number {
            font-size: 52px;
            line-height: 60px;
            display: block;
            padding-bottom: 10px;
            margin-bottom: 10px;
            border-bottom: 1px solid white;
        }
    }
    .description {
        padding-left: 40px;
    }
}
