/* ----------------------------------------
Base Styles
---------------------------------------- */
*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*:before, :before, *:after, :after {
  display: inline-block;
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-family: "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  font-size: 16px;
  line-height: 28px;
}

body.disable-scroll {
  overflow-y: hidden;
  -webkit-overflow-scrolling: none;
}

a {
  transition: all 250ms ease;
}

a img {
  border: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  width: 1080px;
  margin: 0 auto;
}

@media only screen and (max-width: 1140px) {
  .container {
    width: 100%;
    padding: 0 30px;
  }
}

.clearfix:after, .grid:after {
  content: '';
  display: table;
  clear: both;
}

.ellipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  max-width: 100%;
}

.vertical-align {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

/* ----------------------------------------
Project Styles
---------------------------------------- */
@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-16px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes grow-2 {
  0% {
    transform: scale(1) rotateZ(0deg) translateX(0) translateY(0);
  }
  100% {
    transform: scale(0.25) rotateZ(5deg) translateX(-8%) translateY(20%);
  }
}

@keyframes grow-3 {
  0% {
    transform: scale(1) rotateZ(0deg) translateX(0) translateY(0);
  }
  100% {
    transform: scale(1.5) rotateZ(12deg) translateX(20%) translateY(5%);
  }
}

@keyframes grow-4 {
  0% {
    transform: scale(1) rotateZ(0deg) translateX(0) translateY(0);
  }
  100% {
    transform: scale(0.75) rotateZ(45deg) translateX(10%) translateY(-2%);
  }
}

@keyframes grow-5 {
  0% {
    transform: scale(1) rotateZ(0deg) translateX(0) translateY(0);
  }
  100% {
    transform: scale(0.1) rotateZ(-30deg) translateX(-40%) translateY(8%);
  }
}

