header {
  position: fixed;
  top: 0;
  z-index: 9999;
  width: 100%;
  height: 110px;
  background-color: transparent;
  pointer-events: none;
}

header #kb-logo {
  position: absolute;
  top: 30px;
  left: 30px;
  z-index: 9997;
  padding: 10px;
  width: 50px;
  height: 50px;
  border: 1px solid #fff;
  border-radius: 50px;
  pointer-events: all;
}

header #kb-logo img {
  width: 28px;
  height: auto;
  padding-top: 4px;
  padding-left: 1px;
}

header #kb-logo:focus {
  outline: none;
}

#main {
  background-color: #00cca5;
  position: relative;
  z-index: 1;
  height: 100vh;
  overflow: hidden;
}

#main .background {
  font-family: "Seiden Sans", sans-serif;
  color: white;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  font-size: 40vw;
  line-height: 36vw;
  word-break: normal;
  filter: opacity(8%);
  text-align: right;
  letter-spacing: -1vw;
  overflow: hidden;
}

#main .background ::selection {
  background: transparent;
}

#main .background ::-moz-selection {
  background: transparent;
}

#main .background > span {
  display: inline-block;
  pointer-events: none;
}

#main .background > span:nth-child(2n + 1) {
  animation: grow-2 75000ms infinite alternate;
}

#main .background > span:nth-child(3n + 1) {
  animation: grow-3 75000ms infinite alternate;
}

#main .background > span:nth-child(4n + 1) {
  animation: grow-4 75000ms infinite alternate;
}

#main .background > span:nth-child(5n + 1) {
  animation: grow-5 75000ms infinite alternate;
}

#main #user-text {
  font-family: "Seiden Sans", sans-serif;
  border: none;
  background: transparent;
  display: block;
  font-size: 50px;
  line-height: 64px;
  font-style: normal;
  border-bottom: 1px solid white;
  padding: 20px 0;
  margin-bottom: 20px;
  width: 100%;
  height: 233px;
  resize: none;
  overflow: hidden;
  color: white;
  -ms-overflow-style: none;
