}

@media only screen and (max-width: 480px) {
  #main #user-text {
    font-size: 26px;
    line-height: 40px;
  }
}

#main #user-text:focus {
  outline: none;
}

#main #user-text.regular {
  font-weight: 400;
}

#main #user-text.bold {
  font-weight: 700;
}

#main #user-text.light {
  font-weight: 300;
}

#main .subtitle {
  font-size: 18px;
  line-height: 30px;
}

.select-container {
  position: relative;
  display: inline-block;
  margin-left: 4px;
}

.select-container:after {
  content: '';
  display: block;
  position: absolute;
  top: 10px;
  right: 8px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6px 4px 0 4px;
  border-color: #00ad8c transparent transparent transparent;
}

.select-container select {
  display: inline-block;
  font-size: 13px;
  font-family: inherit;
  vertical-align: top;
  padding: 6px 24px 4px 6px;
  border-radius: 2px;
  border: none;
  color: #00ad8c;
  appearance: none;
  cursor: pointer;
  background-color: white;
  box-shadow: none;
}

.button-container {
